/* Boton CTA*/
.button {
    all: unset;
    display: flex;
    align-items: center;
    position: relative;
    padding: 0.6em 1em;
    border: #ABC07F solid 0.15em;
    border-radius: 0.25em;
    color: rgb(2, 44, 28);
    font-size: 1.5em;
    font-weight: 600;
    cursor: pointer;
    overflow: hidden;
    transition: border 300ms, color 300ms;
    user-select: none;
    min-width: 182px;
  }
  
  .button p {
    z-index: 1;
  }
  
  .button:hover {
    color: #212121;
  }
  
  .button:active {
    border-color: #ABC07F;
  }
  
  .button::after, .button::before {
    content: "";
    position: absolute;
    width: 9em;
    aspect-ratio: 1;
    background: #ABC07F;
    opacity: 50%;
    border-radius: 50%;
    transition: transform 500ms;
  }
  
  .button::before {
    left: 0;
    transform: translateX(-8em);
  }
  
  .button::after {
    right: 0;
    transform: translateX(8em);
  }
  
  .button:hover:before {
    transform: translateX(-1em);
  }
  
  .button:hover:after {
    transform: translateX(1em);
  }
  
  .button:active:before,
  .button:active:after {
    background: teal;
  }

