/* ContactForm.css */
  
  .contact-form-container {
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    min-height: 100vh;
    background: #E5E5E5;
    color:black;
  }
  
  @media screen and (min-width: 600px) {
    .auth-form-container {
      padding: 5rem;
      border: 1px solid white;
      border-radius: 10px;
      margin: 0.5rem;
    }
  }
  .contact-form {
    max-width: 400px;
    margin: 50px auto;
    padding: 2rem;
    border: 1px solid white;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
  }

  h4 {
    margin-bottom: 1rem;
    margin-top: 0;
    /* Puedes ajustar el valor según la separación deseada */
  }
  
  .form-field {
    display: flex;
    flex-direction: column;
    margin-bottom: 1rem;
  }
  
  label {
    text-align: left;
    padding: 0.25rem 0;
  }
  
  input,
  textarea {
    margin: 0.5rem 0;
    padding: 1rem;
    border: none;
    border-radius: 10px;
  }
  
  .button-contact {
    border: #ABC07F solid 0.15em;
    background-color: #ABC07F;
    transition: border 300ms, color 300ms, transform 0.3s;
    padding: 20px;
    border-radius: 10px;
    cursor: pointer;
    color: rgb(2, 44, 28);
    position: relative;
    overflow: hidden;
  }
  
  .button-contact::before,
  .button-contact::after {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    background: #ABC07F;
    z-index: -1;
    transition: transform 0.3s;
  }
  
  .button-contact::before {
    transform: scaleX(0);
    transform-origin: right;
  }
  
  .button-contact::after {
    transform: scaleY(0);
    transform-origin: bottom;
  }
  
  .button-contact:hover::before,
  .button-contact:hover::after {
    transform: scaleX(1) scaleY(1);
    transform-origin: left top;
  }
  
  .button-contact:hover {
    color: #bf4f4e;
    background-color: #b4d766;
    font-weight:bolder; 
  }
  